// Serviciu de autentificare pentru validarea datelor de logare
import usersData from '../data/users.json';

export interface User {
  username: string;
  passwordHash: string;
  name: string;
  email: string;
  jobTitle: string;
  department: string;
  location: string;
  manager?: string;
  description?: string;
}

// Interfa pentru stocarea parolelor actualizate
interface UpdatedPasswords {
  [username: string]: string; // username -> passwordHash actualizat
}

/**
 * Obine dicionarul cu parolele actualizate din localStorage
 */
function getUpdatedPasswords(): UpdatedPasswords {
  try {
    const stored = localStorage.getItem('updatedPasswords');
    return stored ? JSON.parse(stored) : {};
  } catch (error) {
    console.error('Eroare la citirea parolelor actualizate:', error);
    return {};
  }
}

/**
 * Salveaz dicionarul cu parolele actualizate n localStorage
 */
function saveUpdatedPasswords(passwords: UpdatedPasswords): void {
  try {
    localStorage.setItem('updatedPasswords', JSON.stringify(passwords));
  } catch (error) {
    console.error('Eroare la salvarea parolelor actualizate:', error);
  }
}

/**
 * Obine hash-ul parolei pentru un utilizator (verific nti n localStorage, apoi n users.json)
 */
async function getUserPasswordHash(username: string): Promise<string | null> {
  // Verific nti n localStorage pentru parole actualizate
  const updatedPasswords = getUpdatedPasswords();
  const lowerUsername = username.toLowerCase();
  
  if (updatedPasswords[lowerUsername]) {
    return updatedPasswords[lowerUsername];
  }

  // Dac nu exist n localStorage, folosete hash-ul din users.json
  const user = (usersData.users as User[]).find(
    u => u.username.toLowerCase() === lowerUsername
  );
  
  return user ? user.passwordHash : null;
}

interface AuthResult {
  success: boolean;
  user?: User;
  message?: string;
}

/**
 * Hash-uiete o parol folosind SHA-256
 */
export async function hashPassword(password: string): Promise<string> {
  const encoder = new TextEncoder();
  const data = encoder.encode(password);
  const hashBuffer = await crypto.subtle.digest('SHA-256', data);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  return hashHex;
}

interface ChangePasswordResult {
  success: boolean;
  message?: string;
}

/**
 * Schimb parola utilizatorului
 * @param username - Numele de utilizator
 * @param oldPassword - Parola veche
 * @param newPassword - Parola nou
 * @param confirmPassword - Confirmarea parolei noi
 * @returns Rezultatul schimbrii parolei
 */
export async function changePassword(
  username: string,
  oldPassword: string,
  newPassword: string,
  confirmPassword: string
): Promise<ChangePasswordResult> {
  // Validri
  if (!oldPassword.trim() || !newPassword.trim() || !confirmPassword.trim()) {
    return {
      success: false,
      message: 'Te rugm s completezi toate cmpurile.'
    };
  }

  if (newPassword.length < 6) {
    return {
      success: false,
      message: 'Parola nou trebuie s aib minim 6 caractere.'
    };
  }

  if (newPassword !== confirmPassword) {
    return {
      success: false,
      message: 'Parolele noi nu se potrivesc.'
    };
  }

  if (oldPassword === newPassword) {
    return {
      success: false,
      message: 'Parola nou trebuie s fie diferit de parola veche.'
    };
  }

  // Verific parola veche (verific nti n localStorage pentru parole actualizate)
  const oldPasswordHash = await hashPassword(oldPassword);
  const userPasswordHash = await getUserPasswordHash(username);

  if (!userPasswordHash) {
    return {
      success: false,
      message: 'Utilizatorul nu a fost gsit.'
    };
  }

  if (userPasswordHash !== oldPasswordHash) {
    return {
      success: false,
      message: 'Parola veche introdus este incorect.'
    };
  }

  // Hash-uiete parola nou
  const newPasswordHash = await hashPassword(newPassword);

  // Salveaz parola nou persistent n localStorage
  const updatedPasswords = getUpdatedPasswords();
  const lowerUsername = username.toLowerCase();
  updatedPasswords[lowerUsername] = newPasswordHash;
  saveUpdatedPasswords(updatedPasswords);

  // Actualizeaz i n 'user' pentru a fi sincronizat cu sesiunea curent
  const userFromStorage = localStorage.getItem('user');
  if (userFromStorage) {
    try {
      const parsedUser = JSON.parse(userFromStorage) as User;
      if (parsedUser.username.toLowerCase() === lowerUsername) {
        parsedUser.passwordHash = newPasswordHash;
        localStorage.setItem('user', JSON.stringify(parsedUser));
      }
    } catch (error) {
      console.error('Eroare la actualizarea parolei n localStorage:', error);
    }
  }

  return {
    success: true,
    message: 'Parola a fost schimbat cu succes!'
  };
}

/**
 * Valideaz datele de logare
 * @param username - Numele de utilizator introdus
 * @param password - Parola introdus
 * @returns Rezultatul validrii (success: true/false i datele utilizatorului dac euiete)
 */
export async function validateLogin(username: string, password: string): Promise<AuthResult> {
  // Verific dac username-ul i parola nu sunt goale
  if (!username.trim() || !password.trim()) {
    return {
      success: false,
      message: 'Te rugm s introduci username-ul i parola'
    };
  }

  // Gsete utilizatorul n list (pentru a obine toate datele)
  const user = (usersData.users as User[]).find(
    u => u.username.toLowerCase() === username.trim().toLowerCase()
  );

  if (!user) {
    return {
      success: false,
      message: 'Username/parola introduse gresit.'
    };
  }

  // Verific parola (verific nti n localStorage pentru parole actualizate)
  const passwordHash = await hashPassword(password);
  const storedPasswordHash = await getUserPasswordHash(username);
  
  if (!storedPasswordHash || passwordHash !== storedPasswordHash) {
    return {
      success: false,
      message: 'Username/parola introdus gresit.'
    };
  }

  // Autentificare reuit
  return {
    success: true,
    user: user
  };
}

/**
 * Returneaz utilizatorul dup username (pentru date de profil)
 */
export function getUserByUsername(username: string): User | undefined {
  return (usersData.users as User[]).find(
    u => u.username.toLowerCase() === username.trim().toLowerCase()
  );
}