// Serviciu pentru gestionarea sistemului de badge-uri
import type { Post } from '../components/MainFeed';

// Interfa pentru un badge
export interface Badge {
  id: string;
  name: string;
  description: string;
  icon: string; // Emoji sau icon
  rarity: 'common' | 'rare' | 'epic' | 'legendary';
  category: 'posts' | 'likes' | 'comments' | 'engagement' | 'milestone';
}

// Interfa pentru cerinele unui badge
export interface BadgeRequirement {
  badge: Badge;
  requirement: string; // Descrierea cerinei
  target: number; // Valoarea int
  current: number; // Valoarea curent
  progress: number; // Procentaj (0-100)
  isEarned: boolean; // Dac badge-ul a fost ctigat
}

// Interfa pentru statisticile utilizatorului
export interface UserStats {
  postsCreated: number;
  likesReceived: number;
  likesGiven: number;
  commentsMade: number;
  commentsReceived: number;
  daysActive: number;
  badges: string[]; // Array de badge IDs
}

